@import url(//db.onlinewebfonts.com/c/f4d1593471d222ddebd973210265762a?family=Pokemon);
.App {
  text-align: center;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(30px + 2vmin);
  font-family: Pokemon;
}

.pokemon-font {
  font-family: Pokemon;
}

.pointer:hover {
  cursor: pointer;
}

.pokemon-text-dec {
  text-decoration: none;
  color: black;
}


.pokemon {
    font-size: medium;
}

.pokemon-img {
    width: 150px;
    height: auto;
}

.pokemon-card {
    width: 500px;
}

.pokemon-abilities h5::first-letter {
    text-transform: uppercase;
}

.pokemon-upper::first-letter {
    text-transform: uppercase;
}

.text-stroke {
    -webkit-text-stroke: 1px black;
}
